<template>
    <v-fab-transition>
      
    <v-btn
      v-show="fab"
      :style="{
        bottom: value ? '80px' : ''
      }"
      aria-label="显示日志"
      bottom
      color="red"
      dark
      fab
      fixed
      large
      right
      :title="fabtitle"
      @click="extendLog"
    >
      <v-icon>{{logicon}}</v-icon>
    </v-btn>
    

    </v-fab-transition>
</template>

<script>
  // Utilities
  import { sync} from 'vuex-pathify'

  export default {
    name: 'HomeFab',

    data: () => ({ 
      myIcon: {
                name: 'mdi-unfold-less-vertical',
                color: 'grey'
            },
            colors: {
                red: 'red',
                green: 'green',
                grey: 'grey'
            },
          fab: false,
    
    }),

    computed: {
      ...sync('snackbar/*'),
      showlog: sync('app/showlog'),
      activeFab () {
      switch (this.tabs) {
        case 'one': return { class: 'purple', icon: 'account_circle' }
        case 'two': return { class: 'red', icon: 'edit' }
        case 'three': return { class: 'green', icon: 'keyboard_arrow_up' }
        default: return {}
      }
      },
      logicon(){
        return this.showlog?"mdi-chevron-right":"mdi-chevron-left" 
      },
      fabtitle(){
        return this.showlog?"隐藏日志" :"显示日志"
      },
      
    },

    methods: {
      extendLog () {
        if (this.showlog){
          console.log("关闭日志窗口");
        }else{
          console.log("显示日志窗口");
        }
        
        this.showlog=!this.showlog;

      },
      changeStatusToUp() {
            this.myIcon.name = 'mdi-chevron-up'
            this.myIcon.color = 'green'
        }
        ,
        changeStatusToDown() {
            this.myIcon.name = 'mdi-chevron-down'
            this.myIcon.color = 'red'
        }
        ,
        changeStatusToMid() {
            this.myIcon.name = 'mdi-unfold-less-vertical'
            this.myIcon.color = 'grey'
        }
    },
    watch: {
    top (val) {
      this.bottom = !val
    },
    right (val) {
      this.left = !val
    },
    bottom (val) {
      this.top = !val
    },
    left (val) {
      this.right = !val
    },
  },
  }
</script>
